<template>
  <scroll-view ref="scroll" class="scroll" refresher :data="data" @pullrefresh="onRefresh">
    <div class="index">
      <div class="name">{{ businessName }}</div>
      <img class="page-img" src='@/assets/manager_home_bg.png' />
      <div class="attestation" @click="bindAuth" v-if="state == '0' || state == '1' || state == '-1'">{{ attestation }}
      </div>
      <div class="close-shop" v-if="data.closeShop == '1'" @click="bindCloseShop">店铺已关店，客户不可下单</div>
      <indexCard @itemClick="bindTransactionData" :data1="data.tradeAmount ? '¥' + data.tradeAmount : '¥0'"
        :data2="data.tradeCount" :data3="data.tradeProductCount" :data4="data.customerUnitPrice" title="交易数据"
        desc1="成交额" :titleTip="transactionTime" titleDesc="经营分析" desc2="成交单数" desc3="成交商品件数" desc4="客单价"></indexCard>

      <indexCard @itemClick="bindOrderManager" :data1="data.unpayCount" :data2="data.undeliveryCount"
        :data3="data.deliveryCount" :data4="data.confirmAfterSalesCount" title="订单管理" desc1="待支付" desc2="商家待发货"
        desc3="易批花已发货" desc4="售后确认">
      </indexCard>

      <indexCard @add="bindAddGoods" @itemClick="bindGoodsManager" :data1="data.allProductCount"
        :data2="data.onsaleProductCount" :data3="data.unsaleProductCount" title="商品管理" desc1="全部" add desc2="出售中"
        desc3="已下架" desc4="添加商品"></indexCard>

      <indexCard @add="bindCreateCoupon" @itemClick="bindCouponList" :data1="data.coupon ? data.coupon.unReceive : '0'"
        :data2="data.coupon ? data.coupon.haveReceive : '0'" :data3="data.coupon ? data.coupon.alreadyUsed : '0'"
        title="优惠券" desc1="待领取券张数" add desc2="已领取券张数" desc3="已使用券张数" desc4="创建优惠券"></indexCard>

      <indexCard @add="bindAddReserveShop" @itemClick="bindReserveShopList" :data1="data.startPresellCount"
        :data2="data.nowPresellCount" :data3="data.finishedCount" title="预售活动管理" desc1="即将预售" add desc2="预售中"
        desc3="已结束预售" desc4="创建预售活动"></indexCard>

      <indexCard @add="bindAddPromotion" @itemClick="bindPromotionManager"
        :data1="data.subCategory ? data.subCategory.applicationCount : '0'"
        :data2="data.subCategory ? data.subCategory.auditCount : '0'"
        :data3="data.subCategory ? data.subCategory.exceedCount : '0'" title="促销活动管理" desc1="全部" add desc2="已生效"
        desc3="已过期" desc4="新增促销活动"></indexCard>
    </div>
  </scroll-view>
</template>

<script>
import indexCard from '@/components/index/IndexCard.vue'
import { index } from '@/http/api'

export default {
  name: 'index',
  data () {
    return {
      data: {},
      transactionTime: '',
      isLogin: false,
      attestation: '',
      state: '',
      businessName: '易批花'
    }
  },
  components: {
    indexCard
  },
  computed: {
    deviceId () {
      return this.$store.state.deviceId
    }
  },
  created () {
    const date = new Date()
    const year = date.getFullYear()
    const month = date.getMonth() + 1
    const day = date.getDate()
    this.transactionTime = year + '年' + month + '月' + day + '日当天'
  },
  mounted () {
    this.getIndexData()
  },
  methods: {
    bindAddPromotion () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$toast('暂无权限')
      // this.$router.push('/addPromotion')
    },
    // 促销商品管理
    bindPromotionManager () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$toast('暂无权限')
      // this.$router.push('/promotionList')
    },
    // 预售活动列表
    bindReserveShopList () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$router.push('/reserveShopList')
    },
    // 创建预售活动
    bindAddReserveShop () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$router.push('/addReserveShop')
    },
    // 创建优惠券
    bindCreateCoupon () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$router.push('/createCoupon')
    },
    // 添加商品
    bindAddGoods () {
      if (this.state === '2') {
        this.$router.push('/releaseGoods')
      } else {
        this.bindAuthResult()
      }
    },
    // 查看订单管理
    bindOrderManager () {
      this.$router.push('/order')
    },
    bindGoodsManager () {
      this.$router.push('/goods')
    },
    // 查看优惠券列表
    bindCouponList () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$router.push('/couponList')
    },
    onRefresh () {
      this.getIndexData()
    },
    getIndexData () {
      index().then(res => {
        this.$refs.scroll.refreshed()
        // console.log('res', res)
        // 显示数据
        this.data = res.data
        this.$store.commit('updateCloseShop', res.data.closeShop)
        const state = res.data.state
        const reason = res.data.reason
        let attestation = null
        if (state === '0') {
          if (reason) {
            attestation = reason
          } else {
            attestation = '请进行店铺认证,即可开启商品发布权限'
          }
        } else if (state === '1') {
          if (reason) {
            attestation = reason
          } else {
            attestation = '店铺正在审核,请耐心等待'
          }
        } else if (state === '-1') {
          if (reason) {
            attestation = reason
          } else {
            attestation = '店铺审核失败,请核对信息重新认证'
          }
        }
        this.attestation = attestation
        this.state = state
        this.businessName = res.data.businessName
        this.isLogin = true
      }).catch(res => {
        this.$refs.scroll.refreshed()
        this.$toast(res.msg)
      })
    },
    bindTransactionData () {
      if (!this.isLogin) {
        this.$toast('用户未登录')
        return
      }
      this.$router.push('/businessAnalysis')
    },
    bindAuth () {

    },
    bindCloseShop () {

    }
  },
  watch: {
    deviceId (to, from) {
      this.getIndexData()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll {
  width: 100vw;
  height: 100vh;
}

.index {
  padding-bottom: 3.125rem;
  box-sizing: border-box;
}

.attestation {
  background-color: #C92434;
  color: white;
  padding: .5rem;
  text-align: center;
  font-size: .875rem;
}

.close-shop {
  background-color: #F08200;
  color: white;
  padding: .5rem;
  text-align: center;
  font-size: .875rem;
}

.name {
  text-align: center;
  padding: .5rem
}

.page-img {
  font-size: 0;
  width: 100%;
  height: auto;
  vertical-align: middle;
}
</style>
