<template>
  <div class="item">
    <div class="item-head" @click="itemClick">
      <div>
        <span class="item-title">{{ title }}</span><span class="title-desc">{{ titleTip }}</span>
      </div>
      <div class="item-hint">
        <span class="item-hint-txt">{{ titleDesc }}</span>
        <img class="item-head-img" src="@/assets/arrow_right_icon.png" />
      </div>
    </div>
    <div class="item-content">
      <div class="item-content-child">
        <span class="data">{{ data1 }}</span>
        <div>{{ desc1 }}</div>
      </div>
      <div class="item-content-child">
        <span class="data">{{ data2 }}</span>
        <div>{{ desc2 }}</div>
      </div>
      <div class="item-content-child">
        <span class="data">{{ data3 }}</span>
        <div>{{ desc3 }}</div>
      </div>
      <div class="item-content-child" @click="addClick">
        <span v-if="add">
          <img class="add-img" src="@/assets/home_add_commoidy_icon.png" />
        </span>
        <span v-else class="data">{{ data4 }}</span>
        <div>{{ desc4 }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'indexCard',
  data () {
    return {

    }
  },
  props: {
    add: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    titleTip: {
      type: String,
      default: ''
    },
    titleDesc: {
      type: String,
      default: ''
    },
    data1: {
      type: [String, Number],
      default: '0'
    },
    data2: {
      type: [String, Number],
      default: '0'
    },
    data3: {
      type: [String, Number],
      default: '0'
    },
    data4: {
      type: [String, Number],
      default: '0'
    },
    desc1: {
      type: String,
      default: '--'
    },
    desc2: {
      type: String,
      default: '--'
    },
    desc3: {
      type: String,
      default: '--'
    },
    desc4: {
      type: String,
      default: '--'
    }
  },
  methods: {
    addClick () {
      if (this.add) {
        this.$emit('add')
      }
    },
    itemClick () {
      this.$emit('itemClick')
    }
  }
}
</script>

<style lang="scss" scoped>
.title-desc {
  color: #9A9A9A;
  margin-left: .625rem;
  font-size: .875rem;
  vertical-align: middle;
}

.item {
  border-bottom: .5rem solid #f1f2f6;
}

.item-head {
  display: flex;
  justify-content: space-between;
  padding: .625rem .75rem;
}

.item-title {
  font-weight: bold;
}

.item-head-img {
  width: .4063rem;
  height: auto;
  margin-left: .25rem;
}

.item-hint {
  color: #636B82;
}

.item-hint-txt {
  vertical-align: middle;
}

.item-head-img {
  vertical-align: middle;
}

.item-content {
  display: flex;
  font-size: .9375rem;
}

.item-content-child {
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;

  div {
    margin-bottom: .6875rem;
    margin-top: .1875rem;
    color: #636B82;
    font-size: .875rem;
  }
}

.add-img {
  height: 1.25rem;
  width: auto;
  font-size: 0;
  vertical-align: middle;
}

.data {
  height: 1.25rem;
}
</style>
